	  import {UniPopup} from 'components/uni/uni-popup/uni-popup'
	  import {KsSort} from 'components/ks-components/sort/sort'
	  import {CollectTkHome,GetCountCollectTk} from 'api/exam'
    export default {
		components:{
			KsSort,
			UniPopup
		},
      data() {
        return {
          allData: {},
          allDataList: [],
          showSort: false,
          categoryid:  0,
          type: 3,
		  baseUrl:this.$store.state.SRC+"/config/app"
        }
      },
      methods: {
		  close(e){
		  	this.showSort = e.show
		  },
        openSort() {
          this.showSort = true;
        },
        closeSort() {
          this.showSort = false;
        },
   
        collectTkHome() {
            
          CollectTkHome({
            categoryid: this.categoryid
          }).then(res=>{
            if (res.result) {
                this.allData = res.data;
            }
          }),
         GetCountCollectTk({
              categoryid: this.categoryid
            }).then(res=> {
              if (res.result) {
                this.allDataList = res.data;
              }
            });
        }
      },
	  onLoad(query) {
	  	this.categoryid = query.categoryid
	  },
      onShow() {
        
     
        this.$store.ready(state=>{
          if(state.isLogin == 1){
            this.collectTkHome();
          }else{
            uni.navigateTo({
            	url:'/pages/user/login/index'
            })
          }
          
        })
        
      }
  };
  